nav {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 20px;
    height: 5%;
    flex-wrap: wrap;
    width: 100%;
}

nav h1 {
    font-size: 2.5rem;
    text-align: left;
    flex-grow: 2;
}

div.units select {
    margin-right: 10px;
    color: #ffffff;
    background: transparent;
    border: none;
    font-size: 1rem;
    cursor: pointer;
}

option {
    color: rgb(22, 6, 6);
}

@media (max-width: 640px) {
    nav {
        justify-content: center;
    }
    nav > * {
        margin: 10px;
    }

    nav >*:first-child {
        margin-top: 0;
    }
}

@media (max-width: 400px) {
    nav h1 {
        width: 100%;
        text-align: center;
    }

    div.units select {
        margin: 0;
    }
}

.search-div {
    position: relative;
    display: flex;
    justify-content: center;
}

.search-result {
    position: absolute;
    top: 40px;
    width: 90%;
    height: auto;
    z-index: 1;
    background: rgba(255, 255, 255, 0.05);
    box-shadow: 0 8px 32px 0 rgba(0, 0, 0, 0.37);
    backdrop-filter: blur(20px);
    -webkit-backdrop-filter: blur(20px);
    border-radius: 10px;
}

.search-result::-webkit-scrollbar{
    width: 5px;
}

.search-result::-webkit-scrollbar-thumb {
    box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
}

.search-result::-webkit-scrollbar-track::-webkit-scrollbar-track{
    background: red;
}

.parent-search-bar {
    display: flex;
    border: 3px solid white;
    border-radius: 25px;
    padding: 2px 5px;
    width: 200px;
    height: 20px;
}

.search-bar {
    background: transparent;
    border: 0;
    margin: 0 5px;
    padding: 3px 0;
    width: 100%;
}

.search-bar::-webkit-input-placeholder {
    color: #ffffff;
    opacity: 0.5;
}

.search-bar:-ms-input-placeholder {
    color: #ffffff;
    opacity: 0.5;
}

.search-bar::placeholder {
    color: #ffffff;
    opacity: 0.5;
}

.search-result-p {
    margin: 15px auto;
    padding-bottom: 5px;
}

.search-result-p:hover {
    cursor: pointer;
    color: rgb(0, 0, 0);
}

input[type="search"]::-webkit-search-decoration,
input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-results-button,
input[type="search"]::-webkit-search-results-decoration {
  display: none;
}

@media (max-width: 640px) {
    .search-div {
        width: 100%;
    }
}
.weather-card {
    width: 100%;
    height: auto;
    display: flex;
    justify-content: center;
    margin-top: 100px;
    margin-bottom: 80px;
}

.cards {
    width: 60%;
    height: auto;
    display: flex;
    flex-wrap: wrap;
}

.card {
    position: relative;
    background: rgba(255, 255, 255, 0.05);
    box-shadow: 0 8px 32px 0 rgba(0, 0, 0, 0.37);
    backdrop-filter: blur(20px);
    -webkit-backdrop-filter: blur(20px);
    border-radius: 10px;
    margin: 5px auto;
    overflow: hidden;
}

.card.loading {
    background: rgba(151, 145, 145, 0.2);
    position: relative;
}

.card.loading::after {
    position: absolute;
    content: "";
    top: 0;
    left: 0;
    width: 200px;
    height: 100%;
    background: rgba(255, 255, 255, 0.15);
    -webkit-transform: translateZ(0);
            transform: translateZ(0);
    -webkit-animation: loadingAfterAnimation infinite 1.5s linear;
            animation: loadingAfterAnimation infinite 1.5s linear;
}

.subcard.card.loading::after {
    width: 120px;
}

@media (max-width: 1024px) {
    .weather-card {
        margin: 0;
        margin-bottom: 20px;
    }
    .cards {
        margin-top: 40px;
    }
}

@media (max-width: 640px) {
    .cards {
        width: 90%;
        margin-top: 0;
    }
}


@media (min-width: 1025px) and (max-width: 1360px) {
    .cards {
        width: 80%;
    }
}

@-webkit-keyframes loadingAfterAnimation {
    from {
        left: -20%;
    }

    to {
        left: 120%;
    }
}

@keyframes loadingAfterAnimation {
    from {
        left: -20%;
    }

    to {
        left: 120%;
    }
}
.main-card {
    width: 49%;
    height: auto;
    margin-right: 1%;
}

.main-card-contents {
    display: flex;
    height: 100%;
    width: 100%;
    padding: 1%;
}

.main-card-contents .main-card-left,.main-card-contents .main-card-right{
    width: 50%;
    height: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}

.main-card-left > *, .main-card-right > * {
    width: 100%;
    margin: 10px 0;
    margin-left: 10px;
    font-weight: bolder;
}

.main-card-right > *  {
    margin: 0;
}

.current-city-name {
    font-size: 2.5em;
}

.main-card-left > span {
    width: 45%;
}

.current-main-temp {
    font-size: 2.2em;
}

.main-current-weather-icon {
    width: 110px;
    height: 110px;
}

.weather-main {
    margin-top: 10px;
}

.current-cloud {
    margin-bottom: 10px;
}

@media (max-width: 1024px) {
    .main-card {
        width: 100%;
        margin-right: 0;
    }
}

@media (max-width: 500px) {
    .current-main-temp {
        font-size: 2em;
    }
    .current-city-name, .weather-main {
        font-size: 2em;
    }
    .main-card-left > span {
        width: 44%;
        font-size: 13px;
    }
    .feels-like {
        font-size: 16px;
    }
}
.sub-cards {
    width: 50%;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-template-rows: 1fr 1fr;
    align-content: center;
    grid-column-gap: 10px;
    justify-content: end;
}

.sub-cards > div {
    width: 100%;
    height: 150px;
}

@media (max-width: 1024px) {
    .sub-cards {
        width: 100%;
        grid-template-columns: 1fr 1fr;
        grid-template-rows: 1fr 1fr;
    }
}
