.uv-content {
    text-align: left;
    padding: 10%;
    padding-top: 5px;
}

.uv-content > * {
    margin: 10px 0;
}

.uv-title {
    display: flex;
}

.uv-title p {
    margin-left: 5px;
}

.uv-index {
    font-size: 2.3em;
}

.uv-indicator-bar {
    width: 100%;
    height: 5px;
    border-radius: 20px;
    background: linear-gradient(to left, #800080 2% ,#ff0000 25%, #ffdb3a 60%, #00FF00 90%);
    position: relative;
}

.uv-indicator {
    position: absolute;
    top: 0;
    width: 1px;
    height: 20%;
    background: rgb(255, 255, 255);
    border: 2px solid grey;
    border-radius: 50%;
}

.uv-desc {
    font-size: 1.3em;
}

@media (min-width: 460px) and (max-width: 1024px) {
    .uv-indicator-bar {
        width: 90%;
    }
}

@media (max-width: 500px) {
    .uv-index {
        font-size: 2em;
    }
    .uv-desc {
        font-size: 1.2em;
        margin-top: 15px;
    }
}

/* for nest hub max */

@media (max-width: 1280px) and (max-height: 800px) {
    .uv-index {
        font-size: 1.8em;
    }
    .uv-desc {
        font-size: 1em;
    }
}
